.hero{
    width: 100%;
    height: 100vh;
    position: relative;
    text-align: center;
}
.hero-mid{
    width: 100%;
    height: 75vh;
    position: relative;
    text-align: center;
}
img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.hero-text{
    position:  absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    letter-spacing: 2px;
}
.hero-text h1{
    font-size: 3rem;
    font-weight: 800;
    background-color: #fff;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.hero-text p{
    font-size: 1.5rem;
    color: #fff;
    padding: 0.5rem 0 2rem 0;

}
.hero-text .show{
    text-decoration: none;
    background-color: #fff;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: #222;
}
@media screen and (max-width:555px){
    .hero-text h1{
        padding: 10px 20px;
    }
    .hero-text p{
        font-size: 1.1rem;
        padding: 0 0 2rem 0;
    }
    .hero-text .show{
        padding: .6rem 1.1rem;
        border-radius: 6px;
        font-size: 1rem;
    }
}

